<template>
    <div class="modal-bg" v-show="show" @mousemove="modalMove" @mouseup="cancelMove">
        <div class="modal-container">
            <div class="modal-header" @mousedown="setStartingPoint">
                警告
                <i class="el-icon-close" @click="hideModal"></i>
            </div>
            <div class="modal-main">
              <p><i class="el-icon-warning"></i>执行删除后将无法恢复，确定继续吗？</p>
            </div>
            <div class="modal-footer alertFooter">
                <el-button type="primary" round @click="submit">确认</el-button>
                <el-button round @click="hideModal">取消</el-button> 
            </div>
        </div>
    </div>
</template>
<script>
  export default {
    name: 'modal',
    props: {
        show: {
            type: Boolean,
            default: false
        },
        id:{

        }       
    },
    computed:{
    },
    data() {
        return {
            x: 0,
            y: 0,
            node: null,
            isCanMove: false,        
        }
    },
    created(){
      console.log(this.show)
    },
    mounted() {
        this.node = document.querySelector('.modal-container')
    },
    methods: {
        hideModal() {
            this.$emit('hideDeleteModal')
        },

        submit(){
            this.$emit('submitDelete',this.id)
        },

        setStartingPoint(e) {
            this.x = e.clientX - this.node.offsetLeft
            this.y = e.clientY - this.node.offsetTop
            this.isCanMove = true
        },

        modalMove(e) {
            if (this.isCanMove) {
                this.node.style.left = e.clientX - this.x + 'px'
                this.node.style.top = e.clientY - this.y + 'px'
            } 
        },
        cancelMove() {
            this.isCanMove = false
        },   
    }
  }
</script>
<style lang="scss" scoped>
@import '../../style/mixin.scss';
@media screen and(min-width: 1380px){
  .modal-bg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.5);
    z-index: 10;
  }
  .modal-container {
    background: #fff;
    border-radius: 10px;
    overflow: hidden;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width:vw(520);
    height:vw(320);
  }
  .modal-header {
    height: vw(60);
    background: #2769fb;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: left;
    cursor: move;
    box-sizing:border-box;
    padding:0 vw(30);
    font-size:vw(20);
  }
  .el-icon-close{
    position: absolute;
    right: vw(30);
    cursor: pointer;
    top:vw(22);
    font-size: vw(20);
  }  
  .modal-footer {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: vw(80);
    border-top: 1px solid #ddd;
  }
  .modal-footer button {
    width: vw(100);
    margin-right:vw(30);
  }
  .modal-main {
    padding: vw(15) vw(30);
    height:vw(180);
    box-sizing:border-box;
    display:flex;
    align-items: center;
    justify-content: center;    
  }
  .modal-main p{
    font-size: vw(20);
    color:#333;
    display:flex;
    align-items: center;
  }
  .el-button.is-round{
    border-radius:6px;
    width:vw(120);
    background-color: #e7eaec;
    border: solid 1px #e7eaec;
    color: #999999;
  }
  .el-button--primary{
    background-color: #2769fb!important;
    color:#fff!important;
  }
  .el-button{
    font-size:vw(20);
  }
  .el-icon-warning{
    font-size:vw1(40);
    color:#ff5d47;
    margin-right:vw1(10)
  } 
}
