            <label class='fw_label'>
                <span class='text_span'>*联系方式：</span>
                <input type="text" placeholder="请输入联系方式">
            </label>
            <label class='user_password fw_label'>
                <span class='text_span'>*密 码：</span>
                <input type="text" placeholder="请输入密码">
            </label>
            <label class='fw_label'>
                <span class='text_span'>*确认密码：</span>
                <input type="text" placeholder="请重新确认密码">
            </label>
            <label class='_curriculum_labeltwo fw_label'>
                <span>*性 别：</span>
                <el-select  slot="prepend" placeholder="请选择" class='_main-rightSelect'>
                    <el-option label="男" value="1"></el-option>
                    <el-option label="女" value="2"></el-option>
                </el-select>
            </label>
            <label class='fw_label checkLabel'>
                <span class='text_span'>*角色授权：</span>
                <section class='section_contener'>
                    <label class='checkbox_label'>
                        <input type="checkbox">
                        <span>财务</span>
                    </label>
                    <label class='checkbox_label'>
                        <input type="checkbox">
                        <span>普通教务</span>
                    </label>
                    <label class='checkbox_label'>
                        <input type="checkbox">
                        <span>教务主管</span>
                    </label>
                    <label class='checkbox_label'>
                        <input type="checkbox">
                        <span>销售</span>
                    </label>
                    <label class='checkbox_label'>
                        <input type="checkbox">
                        <span>销售主管</span>
                    </label>
                </section>
            </label>
            <label class='fw_label checkLabel'>
                <span class='text_span'>*角色授权：</span>
                <section class='section_contener'>
                    <label class='checkbox_label'>
                        <input type="checkbox">
                        <span>营销主管</span>
                    </label>
                </section>
            </label>
        </section>
        <span slot="footer" class="dialog-footer">
            <el-button type="primary" size='mini' @click="userThis.addUserShow = false">保存</el-button>
            <el-button @click="userThis.addUserShow = false" size='mini'>取消</el-button>
        </span>
        </el-dialog>
    </div>
</template>
<script>
export default {
  name: 'adduser',
  data () {
    return {
      dialogVisible: true,
      form: {}
    }
  },
  inject: ['userThis']
}
</script>
