<template>
	<div class='_headerBox'>
		<div class="leftBox">
			<div class="mechanism">
				<el-dropdown>
					<span class="el-dropdown-link">
						{{userLoginInfo.config.name}}<i class="el-icon-arrow-down el-icon--right"></i>
					</span>
					<el-dropdown-menu slot="dropdown">
						<el-dropdown-item>黄金糕</el-dropdown-item>
						<el-dropdown-item>狮子头</el-dropdown-item>
					</el-dropdown-menu>
				</el-dropdown>
			</div>    		
			<ul class="navBox">
				<li v-for="(item,index) in items" :key='index' @click.stop='activeList(index)' >
					<a :class="[{'action': index == indexthis.num-1},item.page]"><span>{{ item.message }}</span></a>
				</li>
			</ul>    		
		</div>   	
		<div class="rightBox">
			<el-dropdown @command="handleCommand">
				<span class="el-dropdown-link">
					<!-- <el-avatar class="user_icon"></el-avatar> -->
					{{userLoginInfo.username}}<i class="el-icon-arrow-down el-icon--right"></i>
				</span>
				<el-dropdown-menu slot="dropdown">
					<el-dropdown-item command='1'>退出登录</el-dropdown-item>
				</el-dropdown-menu>
			</el-dropdown>
			<el-link :underline="false">客服</el-link>
			<el-link :underline="false">帮助</el-link> 
		</div>
	</div>
</template>
<script>
import {mapState} from 'vuex'
import {exitLogin} from '../../../getserver/server'
import {removeSessionStorage} from '../../../cookie/cookie'
export default {
  name: 'headerui',
  data () {
	return {
	items: [
		{message: '我的桌面', page: 'page1'},
		{message: '课程表', page: 'page2'},
		{message: '学生', page: 'page3'},
		{message: '考勤管理', page: 'page4'},
		{message: '日常管理', page: 'page5'},
		{message: '统计', page: 'page6'}
	]
	}
  },
  inject: ['indexthis'],
  mounted() {
	console.log(this.userLoginInfo)
  },
  computed: {
	...mapState(['userLoginInfo'])
  },
  methods: {
	handleCommand (str) {
	  // 退出登录
	  switch (parseInt(str)) {
		case 1:
			// 退出登录
	  		this.exitLigin()
		  break;
	  }
	},
	activeList (i) {
		// 点击
		this.indexthis.num = i + 1
	},
	async exitLigin () {
	  let exit = await exitLogin()
	  if (exit.status) {
		removeSessionStorage('userLoginInfo')// 删除cookie
		this.$router.replace({path: '/'})
	  }
	}
  }
}
</script>
<style lang="scss" scoped>
._headerBox{
	height: 80px;
}
.mechanism{
	display:inline-block;
	line-height:100%;
	vertical-align: middle;
	display: table-cell;
	padding-left:3.125%;
	width: 26.97%;
	font-family: SourceHanSansCN-Bold
}
.mechanism .el-dropdown{
	font-size:20px;
	color:#fff;
}
.mechanism .el-dropdown-link .el-icon--right:before{
	content: "\e790";
}	
.leftBox{
	width:60.85%;
	float: left;
	height:100%;
	display: table;
}
.rightBox{
	width:39.15%;
	float: left;
	height:28%;
	margin-top: 28px;
	text-align:right;
	padding-right: 3.125%;
	box-sizing: border-box;
}
.navBox{
	overflow: hidden;
	height:100%;
	display: table;
	width: 100%;
}
.navBox li{
	width:16.667%;
	display:block;
	vertical-align: middle;
	text-align: center;
	display: table-cell;
	text-align:center;
}
.navBox li a{
	// width:61.54%;
	width:80px;
	display:block;
	height:100%;
	margin-left: 19.23%;
	cursor: pointer;
	border-radius: 12px;
	position: relative;
	background:url("../../../../static/icon/nav_icon_desktop.png") center 25% no-repeat;
	background-size:30% auto;
	font-size:14px;
}
.navBox li a span{
	width:100%;
	text-align:center;
	display:block;
	position:absolute;
	left:0;
	bottom:15%;
}
.navBox li a.page2{
	background:url(../../../../static/icon/nav_icon_timetable.png) center 25% no-repeat;
	background-size:30% auto;		
}
.navBox li a.page3{
	background:url(../../../../static/icon/nav_icon_student.png) center 25% no-repeat;
	background-size:30% auto;		
}	
.navBox li a.page4{
	background:url(../../../../static/icon/nav_icon_attendance.png) center 25% no-repeat;
	background-size:30% auto;		
}	
.navBox li a.page5{
	background:url(../../../../static/icon/nav_icon_daily.png) center 25% no-repeat;
	background-size:30% auto;		
}	
.navBox li a.page6{
	background:url(../../../../static/icon/nav_icon_statistics.png) center 25% no-repeat;
	background-size:30% auto;		
}	
.navBox li a:hover{
	background-color:rgba(255,255,255,0.3);
}
.action{
	background-color:rgba(255,255,255,0.3)!important;
}
.rightBox .el-dropdown-link {
	cursor: pointer;
	color: #409EFF;
}
.rightBox .el-icon-arrow-down {
	font-size: 12px;
}
.rightBox .user_icon{
	width: 28px;
	height: 28px;
	border-radius:50%;
	border: 2px solid #fff;
	background:url(../../../../static/header_back/LOGO_1024.png) center center no-repeat;
	background-size:100% auto;
	margin-right: 5px;
}
.rightBox .user_icon i {
	display:none
}
.rightBox .el-dropdown-link{
	height: 28px;
	line-height: 28px;
	display: flex;
	color: #ffffff;
}
.el-dropdown-link .el-icon--right{
	line-height: 28px;
}
.rightBox .el-dropdown-link .el-icon--right:before{
	content: "\e790";
}
.rightBox .el-link{
	color:#fff;
	display:inline-block;
	height:28px;
	line-height:28px;
	margin-left:5.263%;
}
.rightBox .el-dropdown{
	vertical-align: top;
}
</style>