<template>
  <el-tabs v-model="activeName" @tab-click="handleClick" v-cloak class="elTabPane">
    <el-tab-pane v-for="item in tabList" :label="item.name" :name="item.value" :key='item.value'>
      <erp-gulp-table :name="'tab'+item.value" v-if='item.value==1 && activeName==1'></erp-gulp-table>
      <set-gulp-meal :name="'tab'+item.value" v-if='item.value==2 && activeName==2'></set-gulp-meal> 
      <gulp-classroom :name="'tab'+item.value" v-if='item.value==3 && activeName==3'></gulp-classroom>
      <gulp-user-ment :name="'tab'+item.value" v-if='item.value==7 && activeName==7'></gulp-user-ment>
      <gulp-salary :name="'tab'+item.value" v-if='item.value==9 && activeName==9'></gulp-salary>
    </el-tab-pane>
  </el-tabs>
</template>
<script>
import erpGulpTable from './gulp-table/gulp-table'
import setGulpMeal from './set-meal/gulp-set-meal'
import gulpClassroom from './classroom/gulp-classroom'
import gulpSalary from './salary/salary'
import gulpUserMent from './user_mment/user_mment'
export default {
  name: 'gulp',
  data() {
    return {
      activeName: '1',
      tabList:[{"name":"课程管理","value":"1"},
        {"name":"套餐管理","value":"2"},
        {"name":"教室管理","value":"3"},
        {"name":"班级管理","value":"4"},
        {"name":"教师管理","value":"5"},
        {"name":"机构信息","value":"6"},
        {"name":"用户管理","value":"7"},
        {"name":"操作记录","value":"8"},
        {"name":"课程薪酬","value":"9"}
      ]
    }
  },
  components: {
    erpGulpTable: erpGulpTable,
    setGulpMeal: setGulpMeal,
    gulpClassroom:gulpClassroom,
    gulpSalary: gulpSalary,
    gulpUserMent: gulpUserMent
  },
  methods: {
    handleClick(tab, event) {
      this.activeName = JSON.stringify(parseInt(tab.index) + 1)
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../../../style/mixin';
@import '../../../style/media';
@import '../../../style/var';

.erp_div{
  @include wh(100%, 100%);
  // background-color: #ffffff;
  // box-shadow: 0px 2px 8px 0px rgba(12, 51, 110, 0.12);
}
.elTabPane /deep/ .el-tabs__content{
  padding:0 3.125%;
  box-sizing:border-box;
}
.elTabPane /deep/ .el-tabs__nav{
  width:100%;
  overflow:hidden;
  box-sizing:border-box;
}
.elTabPane /deep/ .el-tabs__item{
  padding:0;
  padding:0 1.6%;
  height:60px;
  line-height:60px;
  font-size:16px;
  color: #333333;
  font-family: SourceHanSansCN-Bold;
}
.elTabPane /deep/ .el-tabs__item.is-active{
  color: #2769fb;
  @include fontColor-defaul($fn-color)
  
}
.elTabPane /deep/ .el-tabs__active-bar{
  height:3px;
  width:20px!important;
  margin:0 1.56%;
  margin-left:1.66%;
  box-sizing:border-box;
  @include bg-defaul($bg-color)
}
.elTabPane /deep/ .el-tabs__nav-scroll{
  margin-left:3.125%;
  box-sizing:border-box;
}
.elTabPane /deep/ .el-tabs__content{
  
}
.elTabPane /deep/ .el-tabs__content .el-tab-pane{
  height:auto;
}
.elTabPane  /deep/ .el-tabs__content{

  .el-tab-pane .table-gulp .div_gulptable .el-table .el-table__header-wrapper table .has-gutter tr{
    @include bgColor-defaul($bgColor-color);
    th{
      @include bgColor-defaul($bgColor-color);
      @include borderColor-defaul($border-color)
    }
  }

  .el-tab-pane .table-gulp .div_gulptable .el-table .el-table__body-wrapper .el-table__body tbody tr{
    @include bgColor-defaul($bgColor-color);
    td {
      @include borderColor-defaul($border-color)
    }
  }

  @include bgColor-defaul($bgColor-color);
}
.elTabPane /deep/ .el-tabs__header{
  margin:0;
  @include bgColor-defaul($bgColor-color);
}
</style>

