<template>
    <!-- 注册 -->
    <div class='logins_conters'>
        <header class='logins_header'>
            <section class='_headersection'>
                <span class='login_head'>注册账号</span>
                <span class='go_registered' @click.stop='go_Login'>去登录</span>
            </section>
        </header>
         <main class='logins_main'>
             <section class='login_input'>
                <label class='user input_label'>
                    <span class='input_label_user spacing'>
                        <em>手</em><em>机</em><em>号：</em>
                    </span>
                    <el-input v-model="form.phone" autocomplete='off' placeholder="请输入手机号码"></el-input>
                </label>
                <label class='input_label verification_code'>
                    <span class='input_label_user spacing'>
                      <em>验</em><em>证</em><em>码：</em>
                    </span>
                    <el-input v-model="form.phonecode" autocomplete='off' placeholder="请输入验证码"></el-input>
                    <el-button @click.stop='getphonecode' v-show='Time_s==0'>获取验证码</el-button>
                    <el-button disabled v-show='Time_s > 0' style='width: 120px;'>{{Time_s}}s重新获取</el-button>
                </label>
                <label class='input_label'>
                    <span class='input_label_user spacing'>
                      <em>密</em><em style='text-align: right;'>码：</em>
                    </span>
                    <el-input v-model="form.password"  autocomplete='off' placeholder="请输入密码" :type='passwordText' @focus='burlInput(comThis)'></el-input>
                </label>
                <label class='input_label'>
                     <span class='input_label_user spacing'>
                        <em>确</em>
                        <em>认</em>
                        <em>密</em>
                        <em>码：</em>
                    </span>
                    <el-input v-model="form.once_again" autocomplete='off' placeholder="请重新确认密码" :type='passwordText' @focus='burlInput(comThis)'></el-input>
                </label>
                <label class='button_label'>
                  <el-button :loading="reginState" @click.stop='go_registered'>立即注册</el-button>
                </label>
                <label class='agreement_label'>
                  <input type="checkbox" :checked="checkedState" @click.stop='checkedClick'>
                  <span class='checkbox_span'>我已阅读并接受<em>《免费琴行管理注册协议》</em></span>
                </label>
             </section>
         </main>
    </div>
</template>
<script>
import axios from 'axios'
import {userregistered, getuserincode} from '../../../getserver/server'
import {setseSsionStorage} from '../../../cookie/cookie'
import {mapMutations} from 'vuex'
export default {
  name: 'registered',
  data () {
    return {
      comThis: this,
      passwordText: 'text',
      reginState: false,
      Time_s: 0, // 验证码获取按钮60秒假死状态
      form : {
        phone: '',// 手机号码
        phonecode: '',// 手机验证码
        password: '', // 密码
        once_again: '' // 再次确认密码
      },
      checkedState: true,
    }
  },
  methods: {
    ...mapMutations(['LOGIN_PAGE']),
    go_Login () {
      setseSsionStorage('login_page', 1)
      this.LOGIN_PAGE('1');
    },
    async userInregister () {
      this.reginState = true
      let register = await userregistered(this.form.phone, this.form.phonecode, this.form.password, this.form.once_again)
      if (register.data.status) {
        // 注册成功
        this.$notify({
          title: '注册成功',
          message: '请完善机构信息',
          type: 'success',
          duration: 1500
        });
        // 初始化
        this.form = {
          phone: '',// 手机号码
          phonecode: '',// 手机验证码
          password: '', // 密码
          once_again: '' // 再次确认密码
        }
        this.Time_s = 0;
        // console.log(register)
        setseSsionStorage('userLoginInfo', register.data.sinfo)
        this.$router.replace({path:'/certification'})
      } else {
        this.$notify.error({
          title: register.data.sinfo,
          message: '这是一条错误的提示消息'
        });
      }
      this.reginState = false;
    },
    checkedClick () {
      this.checkedState = !this.checkedState;
    },
    go_registered () {
      // 立即注册
      if (!this.checkedState) {
        this.$message({
          message: '警告! 请确认已阅读《免费琴行管理注册协议》',
          type: 'warning'
        });
        return
      }
      if (this.form.password != this.form.once_again) {
        this.$message.error('错误!两次密码必须相同')
        return
      }
      this.userInregister()
    },
    getphonecode () {
      // 获取手机验证码
      const phoneZz = /^1[3859]\d{9}$/; // 手机正则验证
      if (phoneZz.test(this.form.phone)){
        // 验证通过
        const that = this
        getuserincode(that.form.phone).then((res)=>{
          if (res.status) {
            // 发送成功
            that.Time_s = 60;// 设置验证码按钮假死时间 60s
            that.$message({
              message: '发送成功,请注意查收',
              type: 'success'
            });
            setTimeout(()=>{
              alert('您的验证码为:'+ res.data.sinfo)
            }, 1500);
            that.TimeServer()
          } else {
            that.$message.error('发送失败')
          }
        }).catch((error)=>{
          console.log(error);
        })
      } else {
        // 验证失败
        this.$message.error('请输入正确的手机号码!');
      }
    },
    TimeServer () {
      // 控制验证码重新获取时间
      const that = this
      let times =function(){
        setTimeout(()=>{
          that.Time_s--
          if (that.Time_s == 0 || that.Time_s == '0') {
            // 60s 已过，关闭定时器
            clearTimeout(times)
            return;
          }
          times()
        }, 1000)
      }
      times()
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../../../style/mixin';
.logins_conters {
    @include wh(26%, 63%);
    min-height: 430px;
    background-color: #ffffff;
    margin-right: 15%;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    .logins_header{
      @include wh(100%, 13%);
      background: url("../../../../static/header_back/login_top_bg.png")no-repeat;
      background-size: 100% 100%;
      border-top-left-radius: 8px;
      border-top-right-radius: 8px;
      padding: 0 7.8%;
      box-sizing: border-box;
      ._headersection {
        @include wh(100%,100%);
        color: #fff;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .login_head{
          font-size: 16px;
        }
      }
      .go_registered{
          font-size: 13px;
          display: flex;
          align-items: center;
          cursor: pointer;
          -moz-user-select:none; /*火狐*/
            -webkit-user-select:none; /*webkit浏览器*/
                -ms-user-select:none; /*IE10*/
                    -khtml-user-select:none; /*早期浏览器*/
                        user-select:none;
        }
        .go_registered::after{
          content: '';
          display: inline-block;
          @include wh(15px,15px);
          background: url("../../../../static/login_icon/icon_switch.png")no-repeat;
          background-size: 100%;
          margin-left: 8px;
        }
    }
    .logins_main {
      flex: 1;
      padding: 7.7% 6.1%;
      box-sizing: border-box;
       .login_input {
          .input_label{
            margin-bottom: 5%;
          }
          .input_label{
            display: flex;
            align-items: center;
            font-size: 13px;
            color: #333333;
            .input_label_user{
              margin-right: 3.4%;
              @include wh(20%,auto);
              display: flex;
              justify-content: space-between;
              em{
                display: inline-block;
                @include wh(100%,100%);
                // letter-spacing: 10px;
              }
            }
            
          }
          .agreement_label{
            @include wh(100%, auto);
            display: inline-block;
            margin-top: 4.2%;
            font-size: 13px;
            color: #999999;
            display: flex;
            align-items: center;
            input[type='checkbox']{
              width: 14px;
              height: 14px;
              background: #fff;
              -webkit-appearance:none;
              border: 1px solid #999999;
              border-radius: 2px;
              outline: none;
              margin-right: 9px;
              cursor: pointer;
            }
            input[type=checkbox]:checked{
              background-size: 100%;
              background: url("../../../../static/icon/icon_unselected.png")no-repeat center;
            }
            .checkbox_span{
              margin-left: 1.8%;
              em{
                color: #2769fb;
                cursor: pointer;
              }
            }
          }
          .button_label{
            margin-top: 7.6%;
            display: inline-block;
            @include wh(100%, 100%);
          }
      }
    }
}
</style>
<style scoped>
.verification_code /deep/ .el-input{
    width: 40%;
    margin-right: 4%;  
}
.button_label /deep/ .el-button{
  width: 100%;
  height: 9.2%;
  background: #2769fb;
  color: #ffffff;
  font-size: 13px;
  border-radius: 8px;
}
.verification_code /deep/ .el-button{
  border: 1px solid #2769fb;
  color: #2769fb;
}
.input_label /deep/ .el-input{
    flex: 1;
}
</style>