      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="current_page"
        :page-size="10"
        layout="total, prev, pager, next, jumper"
        :total="counts*10">
      </el-pagination>
    </div>
</template>
<script>
export default {
  name: 'paging',
  data () {
    return {
      current_page: 1// 初始化页数位置
    }
  },
  props: {
    counts: Number // 总页码
  },
  created () {
    console.log(this.counts)
  },
  methods: {
    handleCurrentChange(val) {
      this.$emit('pagings', val)
    }
  },
}
</script>
<style lang="scss" scoped>
@import '../../style/mixin';
._paging{
  @include wh(100%, 36px);
  display: flex;
  justify-content: flex-end;
  line-height: 36px;
  // padding-right: 60px;
  background: #fff;
  box-sizing: border-box;
  color: #666666;
  .btn-left{
    margin-left: 16px;
  }
  .erp-button{
    background: none;
    border: solid 1px #e7eaec;
    border-radius: 4px;
    padding: 0 12px 0 14px;
    color: #666666;
    font-size: 16px;
    display: flex;
    align-items: center;
    cursor:pointer;
  }
  .icon-btn-width{
    display: inline-block;
    @include wh(20px, 20px)
  }
  .label_right{
    input{
      @include wh(48px, 100%);
      border: 1px solid #e7eaec;
      border-radius: 6px;
      text-align: center;
      font-size: 16px;
      color: #666666;
      margin: 0 6px 0 6px;
    }
    button{
      margin-left: 8px;
