      border-radius: 6px;
      background-color: #ffffff;
      font-size: 16px;
      color: #666666;
      border: solid 1px #e7eaec;
      cursor:pointer;
    }
  }
  ul{
    display: flex;
    ol{
        li{
            display: flex;
            align-items: center;
            justify-content: center;
            .icon-width{
                @include wh(20px,20px);
                display: inline-block;
            }
        }
    }
    li{
        @include wh(36px, 36px);
        border: solid 1px #e7eaec;
        border-radius: 6px;
        text-align: center;
        cursor: pointer;
        margin-right: 4px;
    }
    li:first-child{
        margin-left: 4px;
    }
    li:last-child{
        margin-right: 4px;
    }
    li:hover{
        background: #e9eaec;
    }
  }
