<template>
    <el-container class='el-container'>
        <erp-headers></erp-headers>
        <erp-main :num='num'></erp-main>
    </el-container>
</template>
<script>
import erpHeaders from '../../components/header/header'
import erpMain from '../../components/main/main'
import {getseSsionStorage} from '../../cookie/cookie'
import {mapState, mapMutations} from 'vuex'
export default {
  name: 'home',
  data () {
    return {
      num: 5
    }
  },
  provide () {
    return {
      indexThis: this
    }
  },
  computed: {
    ...mapState(['userLoginInfo'])
  },
  methods: {
    ...mapMutations(['USER_INFO'])
  },
  created () {
    if (getseSsionStorage('userLoginInfo') !=null || getseSsionStorage('userLoginInfo') != ''){
      let jsons = JSON.parse(getseSsionStorage('userLoginInfo'))
      this.USER_INFO(jsons)
    }
  },
  components: {
    erpHeaders: erpHeaders,
    erpMain: erpMain
  },
  beforeRouteEnter (to,from,next) {
    next(vm => {
      // vm实例
      let userinfor = JSON.parse(getseSsionStorage('userLoginInfo'))
      if (typeof userinfor != 'object' || userinfor == null) {
        vm.$router.replace({path: '/'});
      }  else if (typeof userinfor == 'object' && userinfor.orgid == null) {
        vm.$router.replace({path: '/certification'});
      }
    })
  }
}
</script>
<style lang="scss" scoped>

</style>

