<template>
    <div class="modal-bg" v-show="show" @mousemove="modalMove" @mouseup="cancelMove">
        <div class="modal-container">
            <div class="modal-header" @mousedown="setStartingPoint">
                {{ title }}
                <i class="el-icon-close" @click="hideModal"></i>
            </div>
            <div class="modal-main">
              <el-form ref="form" :model="form" :rules="rules" label-width="110px" class="fromBox">
                <el-form-item prop="name" label="教室名称：" >{{fullName}}
                  <el-input v-model="form.name" placeholder="请输入教室名称"></el-input>
                </el-form-item>
                <el-form-item prop="capacity" label="容纳量：" >
                  <el-input v-model="form.capacity" placeholder="请输入容纳量"></el-input>
                </el-form-item>
                <el-form-item prop="classRoom" label="教室状态：" >
                  <el-select v-model="form.classRoom" placeholder="请选择教室状态" @change="currentSel">
                    <el-option label="可用" value="1"></el-option>
                    <el-option label="不可用" value="2"></el-option>             
                  </el-select>
                </el-form-item>                                              
              </el-form>
            </div>
            <div class="modal-footer alertFooter">
                <el-button type="primary" round @click="submit">保存</el-button>
                <el-button round @click="hideModal">取消</el-button> 
            </div>
        </div>
    </div>
</template>
<script>
  import {editclassRoom} from '../../../../../getserver/getAxios.js' 
  export default {
    name: 'modal',
    props: {
        show: {
            type: Boolean,
            default: false
        },
        title: {
            type: String,
            default: ''
        },
        classRoomData: {
          type: Object,
          default: ''          
        },
        orgid: {
          type: String,
          default: false
        }         
    },
    computed: {
      fullName: function () {
        this.form.name = this.classRoomData.name;
        this.form.capacity = this.classRoomData.total;
        this.form.id = this.classRoomData.id;
        this.form.status = this.classRoomData.status;
        if(this.classRoomData.status == 2){
          this.form.classRoom = '不可用';
        }
        else{
          this.form.classRoom = '可用';
        }
        console.log(this.classRoomData)
      }
    },
    data() {
        return {
            x: 0,
            y: 0,
            node: null,
            isCanMove: false,
            form: {
              name: '',
              capacity: '',
              status:'',
              classRoom:''
            },
            rules: {
              name: [
              { required: true, message: '请输入教室名称', trigger: 'blur' },
              { min: 0, max: 10, message: '长度在 0 到 10 个字符', trigger: 'blur' }
              ],
              capacity: [
              { required: true, message: '请输入容纳量', trigger: 'blur' },
              { min: 0, max: 10, message: '长度在 0 到 10 个字符', trigger: 'blur' }
              ],              
              classRoom: [
              { required: true, message: '请选择教室状态', trigger: 'change' }
              ],              
            }             
        }
    },
    created(){
      
    },
    mounted() {
        this.node = document.querySelector('.modal-container')
    },
    methods: {
        hideModal() {
            this.$emit('hideModal')
        },
        submit() {
          var id = this.form.id;
          var a = this.form.name;
          var b = Number(this.form.capacity);
          var c = this.form.status;
          this.editclassRoom (id,a,b,c)
        },

        setStartingPoint(e) {
            this.x = e.clientX - this.node.offsetLeft
            this.y = e.clientY - this.node.offsetTop
            this.isCanMove = true
        },

        modalMove(e) {
            if (this.isCanMove) {
                this.node.style.left = e.clientX - this.x + 'px'
                this.node.style.top = e.clientY - this.y + 'px'
            } 
        },
        cancelMove() {
            this.isCanMove = false
        },
        async editclassRoom (id,a,b,c) {
          let resDate = await editclassRoom(id,a,b,c,this.orgid)
          if (resDate.data.status) {
            this.$message({
              type: 'success',
              message: resDate.data.sinfo
            });
            this.$emit('submit')
          }
        },
        currentSel(selVal) {
         this.form.status = selVal;
         console.log(selVal);
        },        
    }
  }
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
@media screen and(min-width: 1380px){
  .modal-bg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.5);
    z-index: 10;
  }
  .modal-container {
    background: #fff;
    border-radius: 10px;
    overflow: hidden;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width:vw(640);
    height:vw(420);
  }
  .modal-header {
    height: vw(60);
    background: #2769fb;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: left;
    cursor: move;
    box-sizing:border-box;
    padding:0 vw(30);
    font-size:vw(16);
  }
  .el-icon-close{
    position: absolute;
    right: vw(30);
    cursor: pointer;
    top:vw(24)
  }  
  .modal-footer {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: vw(80);
    border-top: 1px solid #ddd;
  }
  .modal-footer button {
    width: vw(100);
    margin-right:vw(30);
  }
  .modal-main {
    padding: vw(15) vw(40);
    height:vw(280);
    box-sizing:border-box;
  }
  .el-button.is-round{
    border-radius:6px;
    width:vw(120);
    background-color: #e7eaec;
    border: solid 1px #e7eaec;
    color: #999999;
  }
  .el-button--primary{
    background-color: #2769fb!important;
    color:#fff!important;
  }
  .el-button{
    font-size:vw(16);
  }
  .fromBox /deep/ .el-form-item__label:before{
    color:#333!important;
    display: inline-block;
    float: left;
  }
  .fromBox /deep/ .el-form-item__label{
    height:vw(40);
    text-align: justify;
    font-size: vw(16);
    vertical-align:top;
  }
  .fromBox /deep/ .el-form-item__label:after {
    content: " ";
    display: inline-block;
    width: 100%;
  }
  .fromBox /deep/ .el-form-item{
    margin-top: vw(30);
    font-size:vw(16);
  }
  .fromBox /deep/ .el-select{
    width:100%
  }  
}
