<template>
    <section class='_curriculumDialog' >
        <!-- 新增课程弹窗 -->
        <div class='erp-dialog' >
          <section class='erpDialogLeft'>
            <header>
              <label>新增课程</label>
              <i class='whitedelete_icon' @click.stop='addSubjectsClick'></i>
            </header>
            <main>
              <section class='_main'>
                <div class='_main-left'>
                  <label>
                    <p>
                      <img src="../../../../../static/header_back/default_bg1.png" width="100%" height="100%"  alt="" v-if='form.listimg==""'>
                      <img :src="Imageport + form.listimg" alt="" v-else>
                    </p>
                    <i>上传课程列表图</i>
                    <input type="file" @change='one_file($event)'>
                  </label>
                  <label>
                    <p>
                      <img src="../../../../../static/header_back/default_bg2.png" width="100%" height="100%"  alt="" v-if='form.infoimg==""'>
                      <img :src="Imageport + form.infoimg" alt="" v-else>
                    </p>
                    <i>上传课程详情背景图</i>
                    <input type="file" @change='two_file($event)' >
                  </label>
                </div>
                <div class='_main-right'>
                  <el-form ref="form" :model="form" >
                    <el-form-item label="*课程名称：">
                      <el-input v-model="form.cur_name" placeholder='请输入课程名称' maxlength='12'></el-input>
                    </el-form-item>
                    <label class='_curriculum_label'>
                      <span>*科&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;目：</span>
                      <el-select v-model="form.subject_id" slot="prepend" placeholder="请选择" class='_main-rightSelect'>
                        <el-option v-for='item in getkuListData' :key='item.sid' :label="item.sname" :value="item.sid"></el-option>
                      </el-select>
                      <el-button slot="append" @click.stop='SubjectsClick'>参考</el-button>
                    </label>
                    <label class='_curriculum_labeltwo'>
                      <span>*授课方式：</span>
                      <el-select v-model="form.tmethods" slot="prepend" placeholder="请选择" class='_main-rightSelect' :disabled='that.repairDate!=null? true : false'>
                        <el-option label="一对一" value="1"></el-option>
                        <el-option label="一对多" value="2"></el-option>
                      </el-select>
                    </label>
                    <label class='_curriculum_label _curriculum_time culum_time'>
                      <span>*时&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;长：</span>
                      <el-input placeholder='填数字，可填范围为15-240' v-model='form.ctime'></el-input>
                      <el-button slot="append">分钟</el-button>
                    </label>
                    <i class='link'></i>
                    <label class='_curriculum_label'>
                      <span>*教师资历(收费标准)：</span>
                    </label>
                    <label class='_charge_label'>
                      <section class='label_left'>
                        <label class='_curriculum_label _curriculum_time'>
                          <span>普&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;通：</span>
                          <el-input placeholder='填数字' v-model='form.tqualific_one'></el-input>
                          <el-button slot="append">元/课时</el-button>
                        </label>
                      </section>
                      <section class='label_right'>
                        <label class='_curriculum_label _curriculum_time'>
                          <span>高&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;级：</span>
                          <el-input placeholder='填数字' v-model="form.tqualific_two"></el-input>
                          <el-button slot="append">元/课时</el-button>
                        </label>
                      </section>
                    </label>
                    <label class='shelves_partlabel culum_time'>
                      <section class='label_left'>
                        <label class='label_checkbox'>
                          <span class='_checkbox_head'>课程上架：</span>
                          <label class='checkbox_conenter' @click.stop='checkboxClick(1)'>
                            <input type="checkbox" :checked='shelvesState'>
                            <span>上架，小程序可查看</span>
                          </label>
                        </label>
                      </section>
                      <section class='label_right'>
                        <label class='label_checkbox' @click.stop='checkboxClick(2)'>
                          <span class='_checkbox_head c2'>通用课:</span>
                          <label class='checkbox_conenter'>
                            <input type="checkbox" :checked='generalState'>
                            <span>设置为通用课</span>
                          </label>
                        </label>
                      </section>
                    </label>
                     <i class='link'></i>
                    <label class='_curriculum_time textarea_tabel'>
                      <span class='textarea_span'>课程描述：</span>
                      <el-input
                          type="textarea"
                          placeholder="可填写课程描述"
                          maxlength="500"
                          show-word-limit
                          v-model='form.describe'
                        >
                        </el-input>
                    </label>
                    <label class='_curriculum_time textarea_tabel'>
                      <span class='textarea_span'>备&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;注：</span>
                      <el-input
                          type="textarea"
                          placeholder="可填写备注说明"
                          maxlength="500"
                          show-word-limit
                          v-model='form.remarks'
                        >
                        </el-input>
                    </label>
                  </el-form>
                </div>
              </section>
              <section class='_mainBtn'>
                <el-button type="primary" size='small ' @click.stop='saveClick' :loading="SumbueShatus">保存</el-button>
                <el-button size='small ' @click.stop='addSubjectsClick'>取消</el-button>
              </section>
            </main>
          </section>
          <transition name='erpSubjects'>
            <section class='erpDialogRight' v-show='DialogRight'>
              <header>
                <label>科目分类参考</label>
                <i class='whitedelete_icon' @click.stop='SubjectsClick'></i>
              </header>
              <main>
                <section class='Rightmain-contener'>
                  <ol class='fx_listol' v-for='item in getkuListData' :key='item.sid'>
                    <ul>
                      <li><span>{{item.sname}}：</span></li>
                    </ul>
                    <li class='fx_listLi'>
                      <em v-for='items in item.pids' :key='items.sid'>{{ items.sname }}</em>
                    </li>
                  </ol>
                </section>
              </main>
            </section>
          </transition>
        </div>
    </section>
</template>
<script>
import Imageport from '../../../../config/ImagePz'
import {getkmList, UpImage, deleteImg, determineKc, getKclist, repairmineKc} from '../../../../getserver/server'
export default {
  name: 'currculum',
  data () {
    return {
      getkuListData: [],
      Imageport,
      shelvesState: false,
      generalState: false,
      SumbueShatus: false,
      form: {
        cur_name: '',// 课程名称
        subject_id: '',// 科目id
        tmethods: '', // 授课方式 1 -> 一对一 2->一对多
        ctime: '',// 课程时长
        tqualific_one: '', // 教师资历费用 格式为 0/0
        tqualific_two: '',
        state: 2, // 1上架 2不上架
        conversion: 2, // 是否转化通用课1是2不是
        describe: '', // 课程描述
        remarks: '', // 备注
        listimg: '', // 列表图片
        infoimg: '' // 详情图片
      },
      DialogRight: false
    }
  },
  created () {
    this.getkmListfun()
  },
  inject: ['that'],
  props: {
    addVisible: Boolean
  },
  mounted () {
    if (this.that.repairDate != null) {
      this.shelvesState = this.that.repairDate.state == 1 ? true : false
      this.generalState = this.that.repairDate.conversion == 1? true : false
      this.form = {
        cur_name: this.that.repairDate.cur_name,// 课程名称
        subject_id: this.that.repairDate.subject.sid,// 科目id
        tmethods: '' + this.that.repairDate.tmethods, // 授课方式 1 -> 一对一 2->一对多
        ctime: this.that.repairDate.ctime,// 课程时长
        tqualific_one: this.that.repairDate.ordinary_tqualific, // 教师资历费用 格式为 0/0
        tqualific_two: this.that.repairDate.senior_tqualific,
        state: this.that.repairDate.state, // 1上架 2不上架
        conversion: 2, // 是否转化通用课1是2不是
        describe: this.that.repairDate.describe, // 课程描述
        remarks: this.that.repairDate.remarks, // 备注
        listimg: this.that.repairDate.listimg, // 列表图片
        infoimg: this.that.repairDate.infoimg // 详情图片
      }
    }
  },
  methods: {
    saveClick() {
      // 保存
      this.setkecC()
    },
    two_file (e) {
      // 上传课程详情图
      if (this.form.infoimg!='') {
        // 重复上传， 先删除后上传
        this.deleteImg(this.form.listimg, e, 1);
        return
      } 
      this.setUpImage(e, 1);
    },
    one_file (e) {
      // 上传列表图
      if (this.form.listimg!='') {
        // 重复上传， 先删除后上传
        this.deleteImg(this.form.listimg, e, 0);
        return
      } 
      this.setUpImage(e, 0);
    },
    async setUpImage (e, i) {
      // 上传 图片
      let files = e.target.files[0];
      let regulaImg = /(.png|.image|.img|.jpeg|.jpg)/;
      if (!regulaImg.test(files.name)) {
        this.$message.error('图片格式不正确');
        return
      }
      let form = new FormData();
      form.append('img', files)
      let setUpImgDate = await UpImage(form)
      if (setUpImgDate.data.status) {
        switch (i) {
          case 0:
            // 列表图上传
            this.form.listimg = setUpImgDate.data.sinfo
            break;
          case 1:
            // 列表详情图上传
            this.form.infoimg = setUpImgDate.data.sinfo
            break;
        }
        return
      }
    },
    async getkmListfun () {
      // 获取科目列表
      let getDatelist = await getkmList()
      if (getDatelist.data.status) {
        // 获取成功
        this.getkuListData = getDatelist.data.sinfo
      }
    },
    async deleteImg (ImgUrl, files, i, k) {
      // 删除图片
      // k 为用户点击取消时传递的参数
      // if(k == 'close') {
      //   if (this.form.listimg!=''){
      //     deleteImg(this.form.listimg);
      //   }
      //   if (this.form.infoimg!=''){
      //     deleteImg(this.form.infoimg);
      //   }
      //   return
      // }
      let deleDate = await deleteImg(ImgUrl);
      if (deleDate.data.status){
        // 删除成功
        this.setUpImage(files, i)
      }
    },
    async setkecC(){
      // 保存课程
      this.SumbueShatus = true
      const _this = this
      if (this.that.repairDate!=null) {
        let curId = ''
        curId = this.that.repairDate.cur_id
        let SetdetermineKc = await repairmineKc(this.form.cur_name,this.form.subject_id,this.form.tmethods, this.form.ctime, this.form.tqualific_one+"/"+this.form.tqualific_two, this.form.state, this.form.conversion, this.form.describe, this.form.remarks, this.form.listimg, this.form.infoimg, curId)
        if (SetdetermineKc.data.status) {
          // 修改课程成功
          this.$message({
            message: SetdetermineKc.data.sinfo,
            type: 'success',
            duration: 2000,
          });
        } 
      } else {
        let SetdetermineKc = await determineKc(this.form.cur_name,this.form.subject_id,this.form.tmethods, this.form.ctime, this.form.tqualific_one+"/"+this.form.tqualific_two, this.form.state, this.form.conversion, this.form.describe, this.form.remarks, this.form.listimg, this.form.infoimg)
          if (SetdetermineKc.data.status) {
            // 添加课程成功
            this.$message({
              message: SetdetermineKc.data.sinfo,
              type: 'success',
              duration: 2000,
            });
            this.form = {
              cur_name: '',// 课程名称
              subject_id: '',// 科目id
              tmethods: '', // 授课方式 1 -> 一对一 2->一对多
              ctime: '',// 课程时长
              tqualific_one: '', // 教师资历费用 格式为 0/0
              tqualific_two: '',
              state: 2, // 1上架 2不上架
              conversion: 2, // 是否转化通用课1是2不是
              describe: '', // 课程描述
              remarks: '', // 备注
              listimg: '', // 列表图片
              infoimg: '' // 详情图片
            }
            getKclist(1).then((res)=>{
              if (res.data.status) {
                _this.that.tableData = res.data.sinfo.data
              }
            })
          } else {
            this.$message({
              message: SetdetermineKc.data.error_msg,
              type: 'error',
              duration: 2000,
            });
          }
        }
        this.SumbueShatus = false
        
    },
    SubjectsClick () {
      // 科目参考click 打开或关闭
      this.DialogRight = !this.DialogRight
    },
    addSubjectsClick () {
      // 新增课程关闭
      this.$emit('close', false);
      // this.deleteImg('','','', 'close')
    },
    emitcurr (i) {
      console.log(i)
    },
    handleClose () {
      this.$emit('close', false)
    },
    checkboxClick (n) {
      switch (n) {
        case 1:
          this.shelvesState = !this.shelvesState
          this.shelvesState?this.form.state=1:this.form.state=2
          break;
        case 2:
          this.generalState = !this.generalState
          this.generalState?this.form.conversion=1:this.form.conversion=2
          break;
      }
    }
  },
  watch:{
    'form.ctime' (value) {
      if (!/^\d+$/g.test(value)){
        this.form.ctime = value.substring(0, value.length-1)
      }
    },
    'form.tqualific_one'(value){
      if (!/^\d+$/g.test(value)){
        this.form.tqualific_one = value.substring(0, value.length-1)
      }
    },
    'form.tqualific_two'(value){
      if (!/^\d+$/g.test(value)){
        this.form.tqualific_two = value.substring(0, value.length-1)
      }
    },
    deep: true
  }
}
</script>
