._curriculumDialog{
  position: fixed;
  z-index: 2000;
  @include wh(100%, 100%);
  min-width: 1280px;
  overflow: hidden;
  background: rgba(0,0,0,0.3);
  top: 0;
  left: 0;
  .erp-dialog {
    @include wh(auto, 76%);
