<template>
    <div class='table-gulp'>
    	<erp-tabtop @getclassRoomLists="getclassRoomLists" @searchclassRoom="searchclassRooms" @selectclassRoom="selectclassRooms" @template="template" @orgid="32"></erp-tabtop>
        <section class='div_gulptable'>
            <el-table
            	ref="table"
                :data="tableData"
                border
                stripe
                resizable=false
                height="540px">
	            <el-table-column
	                prop="index"
	                show-overflow-tooltip
	                align='center'
	                min-width='13.33%'
	                label="序号">
	                <template slot-scope="scope"><span>{{scope.$index+ 1}} </span></template>
	            </el-table-column>
	            <el-table-column
	                prop="name"
	                show-overflow-tooltip
	                align='center'
	                min-width='17.77%'
	                label="教室名称">
	            </el-table-column>
	            <el-table-column
	                prop="total"
	                show-overflow-tooltip
	                align='center'
	                min-width='17.77%'
	                label="容纳量">
	            </el-table-column>
	            <el-table-column            
	            	show-overflow-tooltip
	                align='center'
	                min-width='17.77%'
	                label="教室状态">
	                <template slot-scope="scope">
	                	<span @click="layerOpen(scope.row,$event)" v-if="scope.row.status == 2">不可用</span>
	                	<span @click="layerOpen(scope.row,$event)" v-else-if="scope.row.status == 1">可用</span>
	                	<!-- <itemp :that="that">{{scope.row.address}}</itemp> -->
	                	<!-- <itemp>{{scope.row.address}}</itemp> -->
	                </template>
	            </el-table-column>
	            <el-table-column
	                label="更多操作"
	                min-width='33.36%'>
	                <template slot-scope="scope" class='erp-call'>
	                    <div class="grid-content bg-purple">
	                        <el-button @click="showAlert(scope.row, scope.$index)" type="text" size="small" class='erp-button-icon'>
	                            <i class='repair_icon icon-width'></i>
	                            修改
	                        </el-button>
	                        <el-button type="text" @click="showDelete(scope.row.id)" size="small"  class='erp-button-icon'>
	                            <i class='delete_icon icon-width '></i>
	                            删除
	                        </el-button>
	                    </div>
	                </template>
                </el-table-column>
            </el-table>
        </section>
        <!-- <erp-paging></erp-paging> -->
        <edit-class-room v-if="hackReset" :show="show" :title="title" :orgid="userLoginInfo.orgid" :classRoomData="classRoomData" @hideModal="hideModal" @submit="submit"></edit-class-room>
        <deleteModal :show="showDe" :id="deleteId" @hideDeleteModal="hideDeleteModal" @submitDelete="submitDelete"></deleteModal>
    </div>
</template>
<script>
import $ from '../../../../script/jquery-vendor.js'
import erpTabtop from './classroom-top/classroom-top'
import erpPaging from '../../../paging/paging'
import editClassRoom from '../classroom/alert-box/editClassRoom'
import {getclassRoomList,delectclassRoom,searchclassRoom,selectclassRoom,template} from '../../../../getserver/getAxios' 
import {mapState} from 'vuex'
// import tableLayer from '../../../../script/tableLayer.js'
export default {
	// ...tableLayer,
  name: 'table-gulp',
  data () {
    return {
      that: this,
      tableHeight: '',
      tableData: [],
	  addVisible: false,// 控制新增课程模态窗
	  value: '',
	  title: '编辑教室',
	  show: false,
	  showDe:false,
	  deleteId:'',
	  classRoomData:{},
	  hackReset:true,  
    }
  },
  computed:{
  	...mapState(['userLoginInfo']),
  },  
  methods: {
    repairChange (data, i) {
      console.log(i)
    },
    deletes (data, i) {
      console.log(i)
    },
    layerOpen:function (a,b) {
    	// console.log($(b.path[1]).width())
    	// console.log(b.currentTarget.getBoundingClientRect())
    	console.log(a)
    	var pt = $(b.path[1]).width();
    	var t = b.currentTarget.getBoundingClientRect();
    	if (pt < t.width) {
    		var that = b.currentTarget;
    		var text = b.path[0].innerText;
    		var l = t.x-10+'px';
    		var t = t.y-10+'px';
    		layer.open({
    			type: 1,
    			fixed: false,
    			icon: 2, 
				offset: [t,l],   //相对定位
				shade: false,
				title: false, //不显示标题
				area: [pt, 'auto'],
				content: text, //捕获的元素，注意：最好该指定的元素要存放在body最外层，否则可能被其它的相对元素所影响
				success: function(layero,index){
				    layero.css({"min-width":pt,"padding":"10px","background-color":"#fff"}).append(layero);    //如果该父级原来没有设置相对定位，那么在追加该弹层之前需要设置
				}
			});
    	}      	
    },
    async getclassRoomLists (n) {
    	let t = (new Date()).getTime();
    	let resDate = await getclassRoomList(n,this.userLoginInfo.orgid,t)
    	if (resDate.data.status) {
    		console.log(resDate.data.data.data)
    		this.tableData = resDate.data.data.data
    	}
    	console.log(resDate.data.data.data)
    },
    async searchclassRooms (name,page) {
    	console.log(name)
    	let resDate = await searchclassRoom(name,page,this.userLoginInfo.orgid)
    	if (resDate.data.status) {
    		this.tableData = resDate.data.data.data
    	}
    	console.log(resDate.data.data.data)
    },
    async selectclassRooms (status,page) {
    	console.log(status)
    	let resDate = await selectclassRoom(status,page,this.userLoginInfo.orgid)
    	if (resDate.data.status) {
    		this.tableData = resDate.data.data.data
    	}
    	console.log(resDate.data.data)
    },
    async template (id) {
    	let resDate = await template(id)
    	if (resDate.data.status) {
    		console.log(resData)
    	}
    },            
    delectclassRooms (id) {
		// 删除教室
		delectclassRoom(id).then((resDate)=>{
			if (resDate.data.status) {
				this.$message({
					type: 'success',
					message: resDate.data.sinfo
				});
				this.getclassRoomLists (1)
			}			
		})		
		// this.$confirm('删除该教室, 是否继续?', '提示', {
		// 	confirmButtonText: '确定',
		// 	cancelButtonText: '取消',
		// 	type: 'warning'
		// }).then(()=>{    	
		// 	delectclassRoom(id).then((resDate)=>{
		// 		if (resDate.data.status) {
		// 			this.$message({
		// 				type: 'success',
		// 				message: resDate.data.sinfo
		// 			});
		// 			this.getclassRoomLists (1)
		// 		}
		// 		else{
		// 			this.$message({
		// 				type: 'info',
		// 				message: resDate.data.error_msg
		// 			});            
		// 		}
		// 		console.log(resDate.data)				
		// 	})
		// })
    },        
    showAlert(a,b){
    	console.log(a)
    	this.classRoomData = a;
    	this.show = true
    },   
    hideModal() {
    // 取消弹窗回调
	    this.hackReset = false
	    this.$nextTick(() => {
	    	this.hackReset = true
	    });        
	    this.show = false
    },
    showDelete(id){
    	this.deleteId = id;
    	this.showDe = true
    },    
    hideDeleteModal(){
    	this.showDe = false
    },
    submitDelete(id){
    	this.showDe = false;
    	this.delectclassRooms(id)
    },
    submit() {
    // 确认弹窗回调
	    this.hackReset = false
	    this.$nextTick(() => {
	    	this.hackReset = true
	    });    
	    this.getclassRoomLists(1)
	    this.show = false
    }    
  }, 
  created: function () {
  	this.getclassRoomLists (1)
  },
  mounted(){
  	this.tableHeight = 540;
	// this.tableHeight = window.innerHeight - this.$refs.table.$el.offsetTop - 50+"px";
    //window.innerHeight:浏览器的可用高度
    //this.$refs.table.$el.offsetTop：表格距离浏览器的高度
    //后面的50：根据需求空出的高度，自行调整
  },
  components: {
  	erpTabtop: erpTabtop,
  	erpPaging: erpPaging,
  	editClassRoom: editClassRoom,
    'itemp':{
    	render: function(h){
    	  return h(
    	  	'p',
    	  	{
    	  	  'class': {
    	  	  	foo: true
    	  	  },
    	  	  on: {
    	  	  	click: function(event){
    	  	  		// console.log($('.foo').width())
    	  	  		console.log(event.currentTarget)
    	  	  		console.log(event.currentTarget.getBoundingClientRect())
    	  	  		console.log($(event))
    	  	  		if (event.offsetWidth < event.scrollWidth) {
    	  	  			var that = event.currentTarget;
    	  	  			var text = that.innerText;
    	  	  			var l = event.currentTarget.getBoundingClientRect().x+'px';
    	  	  			var t = event.currentTarget.getBoundingClientRect().y+'px';
    	  	  			var w = that.offsetWidth;
    	  	  			window.layer.open({
    	  	  				type: 1,
    	  	  				fixed: false,
    	  	  				icon: 2, 
				                offset: [t,l],   //相对定位
				                shade: false,
				                title: false, //不显示标题
				                area: ['150px', 'auto'],
				                content: text, //捕获的元素，注意：最好该指定的元素要存放在body最外层，否则可能被其它的相对元素所影响
				                success: function(layero,index){
				                console.log(layero);
				                layero.css("min-width",w).append(layero);    //如果该父级原来没有设置相对定位，那么在追加该弹层之前需要设置
				            }
				        });
    	  	  		}  
    	  	  	}
    	  	  }
    	  	},
    	  	this.$slots.default
    	  )
    	},
    	props: {
    		that: Object
    	}
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../style/mixin';
.div_gulptable{
    @include wh(100%,auto);
    background-color: #ffffff;
    box-shadow: 0px 2px 8px 0px rgba(12, 51, 110, 0.12);
}
.icon-width{
    @include wh(20px, 20px);
    display: inline-block;
    margin-right: vw(6);
    font-size: vw(16);
    color: #666666;
}
.grid-content{
    text-align: center;
    button:nth-child(2n){
        margin-right: vw(50);
        margin-left: vw(50);
    }
}
.el-pagination{
    padding-right: vw(60);
    margin-top: vw(60); 
}
.div_gulptable /deep/.erp-button-icon span{
	font-size:vw(16);
}
</style>
