<template>
    <div >
        <erp-tabtop @setMealList="setMealList" @searchsetMeal="searchsetMeals" @selectsetMeal="selectsetMeal" :orgid="orgid" :manager="manager"></erp-tabtop>
        <section class='div_gulptable'>
            <el-table
                ref="table"
                :data="tableData"
                border
                stripe
                resizable=false
                height="540px">
	            <el-table-column
	                prop="index"
	                show-overflow-tooltip
	                align='center'
	                min-width='16.667%'
	                label="序号">
                    <template slot-scope="scope"><span>{{scope.$index+ 1}} </span></template>
	            </el-table-column>
	            <el-table-column
	                prop="meal_name"
	                show-overflow-tooltip
	                align='center'
	                min-width='13.333%'
	                label="套餐名称">
	            </el-table-column>
	            <el-table-column
                    show-overflow-tooltip
                    align='center'
	                min-width='13.333%'
	                label="课程名称">
                    <template slot-scope="scope">
                        <span @click="layerOpen(scope.row,$event)">{{scope.row.meals_cur}}</span>
                    </template>                    
	            </el-table-column>
	            <el-table-column
	                show-overflow-tooltip
	                align='center'
	                min-width='13.333%'
	                label="套餐价">
                    <template slot-scope="scope">
                        <span>￥{{scope.row.price}}</span>
                    </template>                    
	            </el-table-column>
	            <el-table-column
	                show-overflow-tooltip
	                align='center'
	                min-width='13.333%'
	                label="状态">
                    <template slot-scope="scope">
                        <span @click="layerOpen(scope.row,$event)" v-if="scope.row.cur_state == 1">启用中</span>
                        <span @click="layerOpen(scope.row,$event)" v-else-if="scope.row.cur_state == 2">已关闭</span>
                    </template>                    
	            </el-table-column>       
	            <el-table-column
	                label="更多操作"
	                min-width='30.001%'>
	                <template slot-scope="scope" class='erp-call'>
	                    <div class="grid-content bg-purple">
	                        <el-button @click="showAlert(scope.row, scope.$index)" type="text" size="small" class='erp-button-icon'>
	                            <i class='repair_icon icon-width'></i>
	                            修改
	                        </el-button>
	                        <el-button type="text" @click="showDelete(scope.row.meal_id)" size="small"  class='erp-button-icon'>
	                            <i class='delete_icon icon-width '></i>
	                            删除
	                        </el-button>
	                    </div>
	                </template>
                </el-table-column>
            </el-table>
        </section>
        <edit-meal v-if="show" :title="title" :orgid="orgid" :meal_id="meal_id" :manager="manager" @hideModal="hideModal" @submit="submit"></edit-meal>
        <deleteModal :show="showDe" :id="deleteId" @hideDeleteModal="hideDeleteModal" @submitDelete="submitDelete"></deleteModal>
    </div>
</template>
<script>
import $ from '../../../../script/jquery-vendor.js'
import erpTabtop from './set-meal-top/set-meal-top'
import erpPaging from '../../../paging/paging'
import editMeal from '../set-meal/alert-box/editMeal.vue'
import {set_meal_list,searchsetMeal,selectsetMeal,delectMeal} from '../../../../getserver/getAxios' 
import {mapState} from 'vuex'
// import tableLayer from '../../../../script/tableLayer.js'
export default {
	// ...tableLayer,
  name: 'table-gulp',
  data () {
    return {
      that: this,
      tableData: [],
      title: '编辑套餐',
      show: false,
      orgid:'',
      manager:'',
      showDe:false,
      deleteId:'',      
      meal_id: Number()
    }
  },
  filters: {//过滤器
　　　　Upper: function (value) {
　　　　　　return value.substr(1)
　　　　}
　　},
  computed:{
    ...mapState(['userLoginInfo']),
  },
  methods: {
    layerOpen:function (a,b) {
    	// console.log($(b.path[1]).width())
    	// console.log(b.currentTarget.getBoundingClientRect())
    	// console.log(b)
    	var pt = $(b.path[1]).width();
    	var t = b.currentTarget.getBoundingClientRect();
    	if (pt < t.width) {
    		var that = b.currentTarget;
    		var text = b.path[0].innerText;
    		var l = t.x-10+'px';
    		var t = t.y-10+'px';
    		layer.open({
    			type: 1,
    			fixed: false,
    			icon: 2, 
				offset: [t,l],   //相对定位
				shade: false,
				title: false, //不显示标题
				area: [pt, 'auto'],
				content: text, //捕获的元素，注意：最好该指定的元素要存放在body最外层，否则可能被其它的相对元素所影响
				success: function(layero,index){
				    layero.css({"min-width":pt,"padding":"10px","background-color":"#fff"}).append(layero);    //如果该父级原来没有设置相对定位，那么在追加该弹层之前需要设置
				}
			});
    	}      	
    },
    async setMealList (page) {
        console.log(this.userLoginInfo.orgid)
        let t = (new Date()).getTime();
        let resDate = await set_meal_list(page,t,this.orgid)
        if (resDate.data.status) {
            this.tableData = resDate.data.data.data
        }
        console.log(resDate.data.data.data)
    }, 
    async searchsetMeals (name,page) {
        let t = (new Date()).getTime();
        let resDate = await searchsetMeal(page,name,t,this.orgid)
        if (resDate.data.status) {
            this.tableData = resDate.data.data.data
        }
        console.log(resDate.data.data.data)
    },
    async selectsetMeal (status,page) {
        let t = (new Date()).getTime();
        let resDate = await selectsetMeal(page,status,t,this.orgid)
        if (resDate.data.status) {
            this.tableData = resDate.data.data.data
        }
        console.log(resDate.data.data.data)
    },
    delectMeal (id) {
        // 删除教室
        delectMeal(id).then((resDate)=>{
            if (resDate.data.status) {
                this.$message({
                    type: 'success',
                    message: resDate.data.sinfo
                });
                this.setMealList (1)
            }           
        })      
    },             
    showDelete(id){
        this.deleteId = id;
        this.showDe = true
    },    
    hideDeleteModal(){
        this.showDe = false
    },
    submitDelete(id){
        this.showDe = false;
        this.delectMeal(id)
    },       
    showAlert(a,b){
        this.meal_id = a.meal_id
        this.show = true
    },
    hideModal() {
        // 取消弹窗回调      
        this.show = false
    },

    submit() {
    // 确认弹窗回调  
    this.setMealList(1)
    this.show = false
    }    
  },
  created: function () {
    console.log(this.userLoginInfo)
    this.orgid = this.userLoginInfo.orgid;
    this.manager = this.userLoginInfo.id.toString();
    this.setMealList(1)   
  },
  mounted(){	 	
  },
  components: {
    erpTabtop: erpTabtop,
    erpPaging: erpPaging, 
    editMeal:editMeal,   
    'itemp':{
    	render: function(h){
    	  return h(
    	  	'p',
    	  	{
    	  	  'class': {
    	  	  	foo: true
    	  	  },
    	  	  on: {
    	  	  	click: function(event){
    	  	  		// console.log($('.foo').width())
    	  	  		console.log(event.currentTarget)
    	  	  		console.log(event.currentTarget.getBoundingClientRect())
    	  	  		console.log($(event))
    	  	  		if (event.offsetWidth < event.scrollWidth) {
    	  	  			var that = event.currentTarget;
    	  	  			var text = that.innerText;
    	  	  			var l = event.currentTarget.getBoundingClientRect().x+'px';
    	  	  			var t = event.currentTarget.getBoundingClientRect().y+'px';
    	  	  			var w = that.offsetWidth;
    	  	  			window.layer.open({
    	  	  				type: 1,
    	  	  				fixed: false,
    	  	  				icon: 2, 
				                offset: [t,l],   //相对定位
				                shade: false,
				                title: false, //不显示标题
				                area: ['150px', 'auto'],
				                content: text, //捕获的元素，注意：最好该指定的元素要存放在body最外层，否则可能被其它的相对元素所影响
				                success: function(layero,index){
				                console.log(layero);
				                layero.css("min-width",w).append(layero);    //如果该父级原来没有设置相对定位，那么在追加该弹层之前需要设置
				            }
				        });
    	  	  		}  
    	  	  	}
    	  	  }
    	  	},
    	  	this.$slots.default
    	  )
    	},
    	props: {
    		that: Object
    	}
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../style/mixin';
.div_gulptable{
    @include wh(100%,auto);
    background-color: #ffffff;
    box-shadow: 0px 2px 8px 0px rgba(12, 51, 110, 0.12);
}
.icon-width{
    @include wh(20px, 20px);
    display: inline-block;
    margin-right: 6px;
    font-size: 16px;
    color: #666666;
}
.grid-content{
    text-align: center;
    button:nth-child(2n){
        margin-right: 50px;
        margin-left: 50px;
    }
}
.el-pagination{
    padding-right: 60px;
    margin-top: 30px; 
}
.div_gulptable /deep/.erp-button-icon span{
    font-size:16px;
}
</style>
