<template>
    <section>
        <user-top></user-top>
        <div class='div_gulptable'>
            <el-table
                :data="tableData"
                stripe
                border
                style="width: 100%"
                resizable=false
                >
                <el-table-column
                prop="date"
                label="序号"
                align='center'
                show-overflow-tooltip
                min-width="7.7%">
                </el-table-column>
                <el-table-column
                label="登录名"
                align='center'
                show-overflow-tooltip
                min-width="8.8%">
                <template slot-scope="scope">
                    <span @click='layerOpen(scope.row, $event)'>
                        {{scope.row.name}}
                    </span>
                </template>
                </el-table-column>
                <el-table-column
                prop="address"
                label="性别"
                align='center'
                show-overflow-tooltip
                min-width='7.7%'>
                </el-table-column>
                <el-table-column
                label="手机"
                align='center'
                show-overflow-tooltip
                min-width='11.11%'>
                <template slot-scope="scope">
                    <span @click='layerOpen(scope.row, $event)'>
                        {{scope.row.address}}
                    </span>
                </template>
                </el-table-column>
                <el-table-column
                prop="address"
                label="状态"
                align='center'
                show-overflow-tooltip
                min-width="7.7%">
                </el-table-column>
                <el-table-column
                prop="address"
                label="所属门店"
                align='center'
                show-overflow-tooltip
                min-width='11.11%'>
                <template slot-scope="scope">
                    <span @click='layerOpen(scope.row, $event)' class='color'>
                        {{scope.row.address}}
                    </span>
                </template>
                </el-table-column>
                <el-table-column
                prop="address"
                label="角色"
                align='center'
                show-overflow-tooltip
                min-width='11.11%'>
                <template slot-scope="scope">
                    <span @click='layerOpen(scope.row, $event)' class='color'>
                        {{scope.row.address}}
                    </span>
                </template>
                </el-table-column>
                <el-table-column
                align='center'
                label="更多操作"
                min-width='34.44%'>
                <template>
                    <div class='funBtn'>
                        <el-button
                            type="text"
                            size="small">
                            <i class='repair_icon icon-width'></i>
                            修改
                        </el-button>
                        <el-button
                            type="text"
                            size="small">
                            <i class='delete_icon icon-width'></i>
                            删除    
                        </el-button>
                        <el-button
                            type="text"
                            size="small">
                            重置密码    
                        </el-button>
                        <el-button
                            type="text"
                            size="small">
                            复职    
                        </el-button>
                        <el-button
                            type="text"
                            size="small">
                            修改权限
                        </el-button>
                        <!-- <el-button
                            type="text"
                            size="small">
                            重置密码
                        </el-button>
                        <el-button
                            type="text"
                            size="small">
                            设置考勤密码
                        </el-button> -->
                    </div>
                </template>
              </el-table-column>
            </el-table>
            <erp-paging ></erp-paging>
            <toview></toview>
            <add-user></add-user>
            <add-role></add-role>
        </div>
    </section>
</template>
<script>
import $ from '../../../../script/jquery-vendor.js'
import erpPaging from '../../../paging/paging';
import userTop from './user_top/userTop';
import toview from './to_view/to_view'
import addUser from './add_user/addUser'
import addRole from './addrole/addrole'
export default {
  name: 'user_mment',
  data () {
    return {
      toviewShow:false,
      addUserShow: false,
      addUserRoleShow: false,
      tableData: [{
          date: '2016-05-02',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-04',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1517 弄'
        }, {
          date: '2016-05-01',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1519 弄'
        }, {
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1516 弄'
        }]
    }
  },
  provide(){
    return {
      userThis: this
    }
  },
  methods: {
    layerOpen:function (a,b) {
        var pt = $(b.path[1]).width();
        var t = b.currentTarget.getBoundingClientRect();
        if (pt < t.width) {
            var that = b.currentTarget;
            var text = b.path[0].innerText;
            var l = t.x-10+'px';
            var t = t.y-10+'px';
            layer.open({
                type: 1,
                fixed: false,
                icon: 2, 
                offset: [t,l],   //相对定位
                shade: false,
                title: false, //不显示标题
                area: [pt, 'auto'],
                content: text, //捕获的元素，注意：最好该指定的元素要存放在body最外层，否则可能被其它的相对元素所影响
                success: function(layero,index){
                    layero.css({"min-width":pt,"padding":"10px","background-color":"#fff"}).append(layero);    //如果该父级原来没有设置相对定位，那么在追加该弹层之前需要设置
                }
            });
        }      	
    }
  },
  components: {
    erpPaging: erpPaging,
    userTop: userTop,
    toview: toview,
    addUser: addUser,
    addRole: addRole
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../style/mixin';
@import '../../../../style/var';

.funBtn{
    display: flex;
    justify-content: center;
    .el-button{
        @include wh(auto, 40px);
        margin-left: vw(44);
    }
    .el-button:first-child{
        margin-left: 0;
    }
}
.icon-width{
    @include wh(20px,20px);
    display: inline-block;
}
.div_gulptable /deep/ .el-table {
    height: 540px;
    background-color: #ffffff;
    -webkit-box-shadow: 0px 2px 8px 0px rgba(12, 51, 110, 0.12);
    box-shadow: 0px 2px 8px 0px rgba(12, 51, 110, 0.12);
}
.color{
  @include fontColor-defaul($fn-color)
}
</style>
